body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background: #0f172a;
  color: #fff;
}

/* Navbar */
.navbar {
  text-align: center;
  padding: 40px;
  background: linear-gradient(135deg, #2563eb, #9333ea);
}

.navbar h1 {
  margin: 0;
  font-size: 2.5rem;
}

/* Courses */
.course-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 25px;
  padding: 40px;
}

/* Card */
.course-card {
  background: #1e293b;
  padding: 25px;
  border-radius: 15px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.course-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
}

.icon {
  font-size: 3rem;
  color: #38bdf8;
  margin-bottom: 15px;
}

.course-card h3 {
  margin-bottom: 10px;
}

.course-card p {
  font-size: 0.95rem;
  opacity: 0.9;
}

.level {
  display: inline-block;
  margin: 15px 0;
  padding: 5px 12px;
  border-radius: 20px;
  background: #334155;
  font-size: 0.8rem;
}

.course-card button {
  padding: 10px 18px;
  border: none;
  border-radius: 25px;
  background: #38bdf8;
  color: #000;
  cursor: pointer;
  font-weight: bold;
}

.course-card button:hover {
  background: #0ea5e9;
}
